import React, { Component } from 'react'
import {Paper, Typography} from '@material-ui/core' 
import '../App.css'

const VideoDetails = ({video}) => {
    if(!video) return <div>Loading...</div>

    const videoSrc = `https://www.youtube.com/embed/${video.id.videoId}`
    
        return (
            <React.Fragment>
                <Paper elevation={6} style={{height: '20.2em'}} className="video-image">
                    <iframe frameBorder="0"  height="100%" 
                    width="100%" title="Video Player" src={videoSrc}/>
                </Paper>
                <Paper elevation={6} style={{padding: '15px'}} className="video-image">
                    <Typography variant="h6">{video.snippet.title} - {video.snippet.channelTitle}</Typography>
                    <Typography variant="subtitle1">{video.snippet.channelTitle}</Typography>
                    <Typography variant="subtitle2">{video.snippet.description}</Typography>
                </Paper>                
            </React.Fragment>
        )
}

export default VideoDetails
